@import url(https://fonts.googleapis.com/css?family=Noto+Sans&display=swap);
html {
  font-size: 100%; }

html,
body {
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: 'Noto Sans', sans-serif;
  font-size: 0.8rem;
  line-height: 1.5;
  font-weight: 300;
  color: #000;
  background-color: #fafafa; }

a {
  color: inherit;
  text-decoration: none; }

ul {
  list-style: none; }

img {
  max-width: 100%;
  display: block;
  height: auto; }

table {
  border-collapse: collapse;
  width: 100%; }

thead {
  width: 100%; }

.table-page__table-container {
  width: 100%;
  overflow: auto; }

.url {
  text-decoration: underline;
  color: #3b2dbb; }

.cell {
  border: 1px solid #b1b1b1; }
  .cell__content {
    min-width: 8rem;
    display: flex;
    position: relative;
    padding: 0.2rem;
    line-height: 0.7rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; }
  .cell__ttl {
    background: #e2e2e2;
    font-weight: 500;
    min-width: 1.6rem;
    text-align: center; }
    .cell__ttl--highlight {
      background: #797777; }
  .cell__field {
    background: transparent;
    border: 0;
    outline: 0;
    width: 100%;
    height: 100%;
    flex-shrink: 1; }
  .cell--current {
    background: #d4d4d4; }
  .cell__currency {
    display: flex;
    color: #000;
    flex-direction: column;
    border-radius: 3px;
    font-size: 0.9rem;
    padding: 0 0.1rem;
    position: absolute;
    right: 0;
    top: 0;
    -webkit-transition: 0.4s;
    transition: 0.4s; }
    .cell__currency:hover {
      cursor: pointer;
      opacity: 0.8;
      width: auto;
      color: #03fc90;
      height: auto; }

.controls__address-line-container {
  display: flex;
  align-items: center;
  margin: 0 0 0 0.6rem; }

.controls__address-line {
  width: 100%;
  margin: 0.4rem;
  border: 0;
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.75);
  padding: 0.3rem; }

